div #heading {
  padding-top: 10px;
  color: white;
  text-align: center;
}
div #description {
  color: white;
  text-align: center;
}

body {
  background-color: black;
}
.App {
  background-color: gray;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input {
  background-color: rgb(235, 186, 52);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
#to-do-form input::placeholder {
  color: white;
}
#to-do-form input:focus, #to-do-form button:focus{
  outline: none;
}#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  background-color: rgb(75, 95, 156);
  color: white;
  font-weight: bolder;
}